import * as process from 'node:process';

import { ReceiverSDKInterface } from '@transai/connector-runtime-sdk';
import { expirationValidatorInLine } from '@xip-online-data/helper-functions';
import {
  BadRequest,
  Created,
  InternalServerError,
  KafkaSourceInterface,
  NotFound,
  Ok,
  UnprocessableEntity,
} from '@xip-online-data/kafka-base-service';
import {
  ActionInterface,
  BaseConnectorConfig,
  KafkaCallbackResponse,
  XodBaseMessageType,
  XodJobType,
} from '@xip-online-data/types';

export class ReceiverSDKService implements ReceiverSDKInterface {
  readonly #IPC_CHANNEL = 'connector-runtime';

  readonly #connectorConfig: BaseConnectorConfig;

  readonly #kafkaServiceInstance!: KafkaSourceInterface;

  readonly #actionConfigs: Array<ActionInterface>;

  readonly responses = {
    ok: Ok,
    created: Created,
    badRequest: BadRequest,
    unprocessableEntity: UnprocessableEntity,
    notFound: NotFound,
    internalServerError: InternalServerError,
  };

  constructor(
    apiConfig: BaseConnectorConfig,
    kafkaServiceInstance: KafkaSourceInterface,
    actions: Array<ActionInterface>,
  ) {
    this.#connectorConfig = apiConfig;
    this.#kafkaServiceInstance = kafkaServiceInstance;
    this.#actionConfigs = actions;
  }

  registerCallback<T extends XodJobType = XodJobType>(
    callbackFunction: (message: T) => Promise<KafkaCallbackResponse>,
    eventType?: string,
    identifier?: string,
  ): void {
    this.#kafkaServiceInstance.setCallbackFunction(
      this.#validateJobMessage(
        expirationValidatorInLine(
          this.#connectorConfig.action?.timeSensitive === true,
          callbackFunction,
        ),
      ),
      eventType,
      identifier,
    );
  }

  getActionConfig(message: XodJobType): ActionInterface | null {
    const actions = this.#actionConfigs.filter((action) => {
      return (
        action.identifier === message.actionIdentifier &&
        action.version === message.actionVersion
      );
    });

    if (actions.length !== 1) {
      return null;
    }

    return actions[0];
  }

  emitEventType<T extends XodJobType = XodJobType>(
    callbackFunction: (message: T) => Promise<KafkaCallbackResponse>,
  ): (message: T) => Promise<KafkaCallbackResponse> {
    return (message: T) => {
      this.#emitToIPCChannel(message.eventType);
      return callbackFunction(message);
    };
  }

  #emitToIPCChannel(message: string): void {
    // Ensure process.send exists before sending
    if (process.send) {
      process.send({
        cmd: this.#IPC_CHANNEL,
        message,
      });
    }
  }

  #validateJobMessage(
    callbackFunction: (message: XodJobType) => Promise<KafkaCallbackResponse>,
  ): (message: XodBaseMessageType) => Promise<KafkaCallbackResponse> {
    return async (message: XodBaseMessageType) => {
      if (message.type !== 'JOB') {
        return this.responses.badRequest('Only Accept JOB types')(message);
      }
      return callbackFunction(message as XodJobType);
    };
  }
}
